				<li>新闻标题</li>
				<li>内容</li>
			</ul>
			<div class="showUser-cnt">
				<div class="showUser-cnt-time">1</div>
				<div class="showUser-cnt-urgent">2</div>
				<div class="showUser-cnt-news">3</div>
				<div class="showUser-cnt-cont">4</div>
			</div>
		</div>
		<el-button type="primary" id="guanlian" @click="guanlian">跳转关联页面</el-button>
		<el-button type="danger" style="margin-top:10px" @click="loginAginAdm">重新登陆</el-button>
	</div>
</template>

<script>
    import axios from "axios";
    import common from "@/utils/common";
    export default {
		name:'showAdmin',
		methods:{
			guanlian(){
				this.$router.push({
					name:'tabsSee',
					param:''
				})
			},
			loginAginAdm(){
				this.$router.push({
					name:'login',
					param:''
				})
			}
		}
    }
</script>

